/* COLOR VARIABLES */

$yellow-bkd: #FDCD3B;
$title: #2A3A3F;
$subtitle: #524e4d;
$paragraph: #93a4aa;
$hr: #d6e1e5;

/* -- GENERAL -- */

body {
	font-family: 'Montserrat', sans-serif;
  margin-top: 50px;
	font-weight: 300;
  font-size: 1.4rem;
	line-height: 1.6;
	color: $paragraph;
}

/* remove blue outline on jquery smooth scroll */
section {
	outline: none;
}

h2 {
	color: $title;
	text-align: center;
	margin-bottom: 20px;
	font-weight: bold;
}

/* ===  LAYOUT ===*/

blockquote {
  padding: 0;
  border: none;
  font-style: italic;
  text-align: left;
}

blockquote cite {
  display: block;
  color: #777;
  margin: 15px 0 0 0;
}

blockquote:before {
  display: none;
}

.row + .row {
  margin-top: 20px;
}

// Global Section Styles

section {
  padding: 80px 0;
	h3 {
	  margin: 30px 0 30px;
		font-weight: 700;
		font-size: 3rem;
	}
	.main-content {
	  padding-top: 40px 0;
	}
}

.section-header {
  text-align: center;
}

#primary {
  padding-top: 40px;
  padding-bottom: 40px;
}


// Restyled Primary Buttons

button.btn:focus {
  outline: none !important;
}

// Optin Section Button Styles
#optin .btn-success {
	background-color: transparent;
	border-radius: 50px;
	border: 2px solid $yellow-bkd;
	color: white;
	font-size: 1.6rem;
	font-weight: 600;
	margin: auto;
	padding: 10px;
	transition: color 0.2s 0.3s, background-color 0.3s ease-in-out, width 0.3s 0s;
	width: 175px;
}

#optin .btn-success:hover {
	width:200px;
	color: $title;
	background-color: $yellow-bkd;
	border: 2px solid $yellow-bkd;
}

//My Work Section Button Styles
#my-work .btn-success {
  border: none;
	border-radius: 50px;
	border: 2px solid $yellow-bkd;
	background-color: transparent;
	color: $title;
	font-size: 1.4rem;
	font-weight: 600;
	margin: auto;
	transition: color 0.2s 0.3s, background-color 0.3s ease-in-out, width 0.3s 0s;
	width: 175px;
}

#my-work .btn-success:hover {
// Styles for stretch effect
	width:200px;
	color: $yellow-bkd;
  background-color: $title;
	border: 2px solid $title;
}

// Contact Section Button Styles
#contact .btn-success {
	 border: none;
	 margin: auto;
	 width: 175px;
	 transition: color 0.2s 0.3s, background-color 0.3s ease-in-out, width 0.3s 0s;
	 border-radius: 50px;
	 border: 2px solid $title;
	 background-color: transparent;
	 color: $title;
}

#contact .btn-success:hover {
	 /* styles for stretch effect: */
	 width:200px;
	 color: $yellow-bkd;
	 border: 2px solid $title;
	 background-color: $title;
}


// Custom Navigation Bar

.logged-in .navbar-fixed-top {
		top: 32px;
}

// Fix the to margin when logged in
@media screen and (max-width: 782px) {
	.logged-in .navbar-fixed-top {
		top: 46px;
	}
}

/*Fix the to margin when logged in */
@media screen and (max-width: 600px) {
	.logged-in .navbar-fixed-top {
		top: 42px;
	}
}

/* === HERO == */

// jquery typed.js plugin styles
.typed-cursor{
    opacity: 1;
    -webkit-animation: blink 0.7s infinite;
    -moz-animation: blink 0.7s infinite;
    animation: blink 0.7s infinite;
}
@keyframes blink{
    0% { opacity:1; }
    50% { opacity:0; }
    100% { opacity:1; }
}
@-webkit-keyframes blink{
    0% { opacity:1; }
    50% { opacity:0; }
    100% { opacity:1; }
}
@-moz-keyframes blink{
    0% { opacity:1; }
    50% { opacity:0; }
    100% { opacity:1; }
}

.headline-type {
	font-size: 3.4rem;
	font-weight: 900;
}

.typing-area {
		font-size: 3.4rem;
		font-weight: 900;
}

section#hero {
	background-size: cover;
}

#hero {
	height: 100vh;
  min-height: 300px;
  padding: 40px 0;
  color: white;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
	article {
	  width: 100%;
	  text-align: center;
		margin-top: 130px;
	}
	.hero-text {
	  margin-top: 30px;
	}
}

@media screen and (min-width: 550px) {
	#hero {
		background: linear-gradient(rgba(193,173,214,0.45), rgba(193,173,214,0.45)),url(assets/images/computer-code-hero) 100% 0 repeat fixed;
	}
}

@media screen and (max-width: 550px) {
	#hero {
		background: none;
		background-color: $title;
	}
}

@media screen and (min-width: 1000px) {
	.headline-type {
		font-size: 5.7rem;
	}
}


@media screen and (min-width: 1000px) {
	.typing-area {
		font-size: 5.7rem;
	}
}

@media screen and (max-width: 750px) {
	.headline-type {
		font-size: 2.3rem;
	}
}

@media screen and (max-width: 750px) {
	.typing-area {
		font-size: 2.3rem;
	}
}


//Optin Section

#optin	p {
		margin: 7px 0 0 0;
 }


/* === MY EXPERTISE == */
#my-expertise {
  text-align: center;
	.my-expertise-title {
		color: $title;
	}
	.my-expertise-subtitle {
		color: $subtitle;
		font-size: 2rem;
		margin-bottom: 30px;
	}
	i.fa {
		font-size: 5.0rem;
		color: $yellow-bkd;
	}
	.my-expertise-paragraph {
		font-weight: 100;
		line-height: 2.5rem;
	}
}


/* === MY WORK === */
#my-work {
	background-color: #eee;
	.my-work-title {
		color: $title;
	}
	ul {
		font-size: 1.1rem;
		font-weight: 700;
		margin-bottom: 20px;
		list-style-position: inside;
		padding: 0;
		list-style: none;
		letter-spacing: 1.5px;

		li::before {
	  	content: "\2022";
			margin-right: 7px;
	  	color: $yellow-bkd;
			font-size: 1.8rem;
		}
	}
	.my-work-paragraph {
		font-weight: 100;
		line-height: 2.5rem;
	}
}

//Image Hover Opacity Effect

	.wrapper {
	  position: relative;
	  padding: 0;
	  background-color: #FDCD3B;
	}

	img{
	  opacity: 1;
	  transition: .5s ease;
	}

	.content {
	  position: absolute;
	  top: 50%;
	  left: 50%;
	  transform: translate(-50%,-50%);
	  text-transform: uppercase;
	  color: #fff;
	  font-size: 60px;
	  white-space: nowrap;
	  overflow: hidden;
	}

	.content .fa {
	  font-size: 7rem;
		color: #fff;
	}

	.overlay {
	  opacity: 0;
	}

	.wrapper:hover img {
	  opacity: .2;
	}

	.wrapper:hover .overlay {
	  opacity: 1;
	}

// End Image Opacity Effect

	p {
		margin-bottom: 30px;
	}


	hr {
  width: 100%;
  border-bottom: 1px solid $hr;
 }


/* === CONTACT SECTION === */
#contact {
  text-align: center;
  background-size: cover;
  color: $title;
  padding: 80px 0 0;
	background-color: $yellow-bkd;
	h2 {
		font-weight: bold;
 }

	h3 {
		font-size: 2rem;
		line-height: 3rem;
		margin-bottom: 40px;
	}
}

footer {
  background: $title;
  color: $paragraph;
  padding: 80px 0 50px;
  font-size: 12px;
	text-align: center;

	p {
	  margin: 20px 0 20px;
	}
}

/* === FORMS === */

label {
  display: block;
  font-weight: bold;
}

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
textarea{
  color: #666;
  border: 1px solid #ccc;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 3px;
  display: block;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  width: 100%;
  padding:  6px 12px;
}

input[type="submit"],
button[type="submit"] {
	font-size: 1.4rem;
	font-weight: 700;
	padding: 10px;
	border: none;
	margin: auto;
	width: 175px;
	transition: color 0.2s 0.3s, background-color 0.3s ease-in-out, width 0.3s 0s;
	border-radius: 50px;
	border: 2px solid #2A3A3F;
	background-color: transparent;
	color: #2A3A3F;
	margin: 25px;
}

input[type="submit"]:hover,
button[type="submit"]:hover {
	outline: none;
	border: none;
	background: #aaa;
	width: 200px;
  color: #FDCD3B;
  border: 2px solid #2A3A3F;
  background-color: #2A3A3F;
}


